import React, { useCallback, useState } from 'react';
import gql from 'graphql-tag';
import { useQuery, useMutation } from 'react-apollo';
import renderComponent from './utils/renderComponent';

const POSTS_QUERY = gql`
  query PostsPage {
    viewer {
      id
    }
    postsAll {
      id
      title
      tagline
      url
      commentsCount
      votesCount
      isVoted
    }
  }
`;

const UPDATE_VOTE = gql`
  mutation PostsPage($postId: String!) {
    voting(postId: $postId) {
      errors
      isVoted
    }
  }
`;

const PostsIndex = () => {
  const [errors, setErrors] = useState([]);
  const { data, loading, error, refetch } = useQuery(POSTS_QUERY);
  const [updateVote] = useMutation(UPDATE_VOTE);


  const handleVoteClick = useCallback(async (e) => {
    const postId = e.target.dataset.id;

    const { data: { voting: { errors } } } = await updateVote({ variables: { postId } });
    if (errors.length) {
      if (errors.includes('current user is missing')) {
        window.location.href = '/users/sign_in'
      }
      setErrors(errors);
    } else {
      refetch();
    }
  }, []);

  if (loading) return 'Loading...';
  if (error) return `Error! ${error.message}`;

  return (
    <div className="box">
      {errors.map((error) => (
        <div key={error} className="notice">
          {error}
        </div>
      ))}
      {data.postsAll.map((post) => (
        <article className="post" key={post.id}>
          <h2>
            <a href={`/posts/${post.id}`}>{post.title}</a>
          </h2>
          <div className="url">
            <a href={post.url}>{post.url}</a>
          </div>
          <div className="tagline">{post.tagline}</div>
          <footer>
            <button onClick={handleVoteClick} data-id={post.id} className={post.isVoted ? 'voted' : ''}>
              {post.isVoted ? '\uD83D\uDD3D' : '\uD83D\uDD3C'} {post.votesCount}
            </button>
            <button>💬 {post.commentsCount}</button>
          </footer>
        </article>
      ))}
    </div>
  );
};

renderComponent(PostsIndex);
